import React, { Component } from "react";
import { extent, sum } from "d3-array";
import { scaleLinear } from "d3-scale";
import { format } from 'd3-format';

import "./histogram.css";

const WIDTH = 100;
const HEIGHT = 120;
const margins = {
  top: 0,
  right: 5,
  bottom: 0,
  left: 40
};

class Histogram extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    const { data, extents } = this.props;
    if (!data) {
      return null;
    }
    // console.log("DATA", data);

    const xScale = scaleLinear()
      //.domain(extent([...data.map(d => d.x0), ...data.map(d => d.x1)]))
      .domain(extents.x)
      .range([0, WIDTH])
      .nice();

    const sumN = sum(data.map(d => d.n));

    const yScale = scaleLinear()
      .domain([0.01, 1]) // ...data.map(d => d.n)]))
      .range([1, HEIGHT - (margins.bottom + margins.top)])
      .nice();

    // console.log('yScale', yScale.ticks())
    return (
      <div
        className="histogram"
        style={{ width: `${WIDTH}%`, height: `${HEIGHT}px` }}
      >
        <div className="histogram-y-axis">
          <div className="ticks">
            {yScale.ticks(5).map(d => {
              const y = yScale(1 - d);
              return <span key={`tick${d}`} className="tick" style={{top:`${y + margins.top}px`}}>
                <span style={{width:`${margins.left - 10}px`}}>{format(',.0%')(d)}</span>
              </span>;
            })}
          </div>
        </div>
        <div className="bars" style={{ marginLeft: `${margins.left}px`, width:`calc(100% - ${margins.left}px)`}}>
          {data.map(d => {
            const barStyle = {
              height: `${yScale(d.n / sumN)}px`
            };
            return (
              <div
                key={`bar${d.x1}-${d.x2}`}
                className="bar"
                style={{ width: `${xScale(d.x1) - xScale(d.x0)}%` }}
              >
                <div style={barStyle}>
                  <span className="label">{d.x0}</span>
                </div>
              </div>
            );
          })}
        </div>

      </div>
    );
  }
}

export default Histogram;



