import React, { Component } from "react";
import PlayersDataViz from "./components/PlayersDataViz/PlayersDataViz";
import Distributions from "./components/Distributions/Distributions";

import competitionsList from "./components/competitions.json";

import CompetitionsComparison from "./components/CompetitionsComparison/CompetitionsComparison";
import TeamsComparison from "./components/TeamsComparison/TeamsComparison";
import PlayersComparison from "./components/PlayersComparison/PlayersComparison";

import "./App.css";

const PLAYERS_THRESHOLD = 3750;

window.getImage = () => {
  const img = document.createElement("img");
  const canvas = document.getElementsByTagName("canvas")[0];
  img.src = canvas.toDataURL();
  document.body.appendChild(img);
};

class App extends Component {
  state = {
    lang: 'en',
  }
  componentDidMount() {
    this.setState({lang: document.querySelector('html').getAttribute('lang')});
  }
  sendAnalytics(player, event = 'search-player') {
    if(!player) {
      return;
    }
    // console.log('sendAnalytics',event,player)
    if(typeof window.gtag !== 'undefined') {
        window.gtag('event',event,{
          'event_category' : 'engagement',
          'event_label': player
        })
    }
  }
  render() {
    const {lang} = this.state;
    return (
      <div className="content-wrapper essay">
        <div className="container-fluid">
          {/*<Header />*/}
          <div className="row no-margin-row">
            <div className="col-xs-12">
              <section>
                <PlayersDataViz file="prefetchedPlayers.json" lang={lang} analyticsCallback={(player) => this.sendAnalytics(player)}/>
              </section>
            </div>
          </div>
          <div className="row">
            <div className="col-xs-12 col-sm-6 col-sm-offset-3">
              <section>
                <h2>I campionati</h2>
                <p>
                  La Bundesliga tedesca &egrave; il campionato in cui un
                  giocatore passa pi&ugrave; tempo nello stesso club, quasi tre
                  anni. Pi&ugrave; in basso la Serie A con periodi di permanenza
                  pi&ugrave; bassi, tuttavia in serie A si registrano alcuni dei
                  casi di giocatore bandiere pi&ugrave; longevi, come Paolo
                  Maldini, Francesco Totti o Alessandro Del Piero.
                </p>
                <CompetitionsComparison
                  competitionsList={competitionsList}
                  playersThreshold={PLAYERS_THRESHOLD}
                />
              </section>
              <section>
                <h2>Le squadre</h2>
                <p>
                  Lista delle squadre con il numero medio anni per giocatore
                  nella stessa squadra. Storia delle squadre basche
                </p>
                <TeamsComparison
                  competitionsList={competitionsList}
                  playersThreshold={PLAYERS_THRESHOLD}
                />
              </section>
              <PlayersComparison
                competitionsList={competitionsList}
                playersThreshold={PLAYERS_THRESHOLD}
              />
              <section>
                <h2>Prima e dopo Bosman</h2>
                <p>
                  La sentenza Bosman ha aperto i confini del calcio nel 1995. La
                  vita di tanti giocatori e' cambiata e molti di loro sono
                  diventati giramondo. Mentre il numero medio di squadre in cui
                  un giocatore milita durante la sua vita non e' cambiata molto,
                  il numero di nazioni visitate dai giocatori e' raddoppiato.
                </p>
                <Distributions
                  file="playersYearsInACountry.json"
                  groupMap={{
                    before: {
                      title: "Prima della sentenza",
                      subtitle: "Anni 1945-1995"
                    },
                    after: {
                      title: "Dopo la sentenza",
                      subtitle: "Anni 1995-2018"
                    }
                  }}
                />
                {/* <section>
                  <h2>Distribuzioni squadre</h2>
                  <h4>Media anni</h4>
                  <Distributions file="playersYearsInATeamAvg.json"/>
                  <h4>Max anni</h4>
                  <Distributions file="playersYearsInATeamMax.json"/>
                </section> */}
                <p>Giocatori per cui abbiamo almeno 5 anni di dati</p>
              </section>
              <section>
                <h2>Data</h2>
                <h4>Stranezze nei dati / Anomalies in the data</h4>
                {/*<p>
                https://www.nytimes.com/interactive/2017/12/29/upshot/internationalization-of-pro-sports-leagues-premier-league.html#wfn-italy-serie-a
                <br/>
                https://statathlon.com/the-impact-of-foreign-players-on-the-performance/
                <br/>
                https://www.researchgate.net/publication/227574479_The_Effects_of_the_Bosman_Ruling_on_National_and_Club_Teams_in_Europe
                </p>*/}
              </section>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



