import React, { Component } from "react";
import "./players-list.css";


class PlayersList extends Component {
  state = {
    selected: [],
  }
  static getDerivedStateFromProps(props, state) {
    // console.log('getDerivedStateFromProps',props,state)
    if(!props.selectedPlayers.length) {
      return {
        selected: [],
      }
    }
    if(props.selectedPlayers.length) {
      return {
        selected: props.selectedPlayers[0],
      }
    }
    return null;
  }
  clickHandler(player) {
    let {selected} = this.state;
    if(this.props.callback) {
      this.props.callback(player, false);
    }
    // // console.log(player,selected,selected.indexOf(player),~selected.indexOf(player))
    // if(!~selected.indexOf(player)) {
    //   // console.log('pushing player',player)
    //   //selected.push(player);
    //   selected=[player];
    // }
    // this.setState({
    //   selected,
    // })
  }
  render() {
    const {players, storyID, title}=this.props;
    const {selected}=this.state;
    // console.log('render selected',selected)
    return (
      <div className="story" data={storyID}>
        <div className="story-chapter">
          <h4>Capitolo 4</h4>
          <h1><span>{title}</span></h1>
        </div>
        <ul className="players-list">
          {players.map(player => {
            let name = player.replace(/-.{3}\s.*$/i,'');
            return <li key={player} className={~selected.indexOf(player) ? 'selected' : ''} onClick={() => this.clickHandler(player)}>{name}</li>
          })}
        </ul>
      </div>
    );
  }
}

export default PlayersList;



