import React, { Component } from "react";
import { extent } from "d3-array";
import Histogram from './Histogram';

import "./distributions.css";

class Distributions extends Component {
  constructor(props) {
    super(props);
    this.state = {}
  }
  async componentDidMount() {
    const res = await fetch(
      `assets/transfers/data/${this.props.file}`
    );

    const distributions = await res.json();

    // console.log(distributions)
    this.setState({
      distributions,
    })
  }

  render() {
    const {distributions} = this.state;
    const {groupMap} = this.props;

    if(!distributions) {
      return null;
    }

    const values = { x: [], y: []};

    Object.values(distributions).forEach(distribution => {
      // console.log(distribution)
      values.x=[...values.x, ...distribution.map(d => d.x0), ...distribution.map(d => d.x1)];
      values.y=[...values.y, ...distribution.map(d => d.n)];
    })

    const extents = {
      x: extent(values.x),
      y: extent(values.y),
    };

    return <div className="distributions">
      {Object.entries(distributions).filter(d => groupMap[d[0]]).map(d => {

        const info = groupMap[d[0]];
        return <div key={d[0]} className="distribution">
          <h5>{info.title}</h5>
          <h6>{info.subtitle}</h6>
          <Histogram key={d[0]} data={d[1]} extents={extents}/>
        </div>;

      })}
    </div>
  }
}

export default Distributions;



