import React, { Component } from 'react';

import './autocomplete.css';

const computeList = (dataList=[], state) => {
  // console.log('dataList', dataList)
  if (dataList.length && dataList.length !== state.listLength) {
    const sortedItems = dataList.sort((a, b) => {
      a.name =
        a.name || (a.label && a.label.replace(/-.{3}\s.*$/i, '').replace(/-n\/a/gi, '')); //  || a.value.split("-")[0];
      b.name =
        b.name || (b.label && b.label.replace(/-.{3}\s.*$/i, '').replace(/-n\/a/gi, '')); // || b.value.split("-")[0];

      const wordA = a.name; // wordArrayA.length < 2 ? wordArrayA[0] : `${wordArrayA[1]} ${wordArrayA[0]}`;
      const wordB = b.name; // wordArrayB.length < 2 ? wordArrayB[0] : `${wordArrayB[1]} ${wordArrayB[0]}`;

      // console.log(wordA,'vs',wordB)

      if (wordB.match(wordA)) return -1;
      if (wordA.match(wordB)) return 1;

      if (wordA < wordB) return -1;
      if (wordA > wordB) return 1;

      // return a.name.length - b.name.length;
      return 0;
    });

    return {
      listLength: dataList.length,
      sortedItems,
      flatList: sortedItems.map(d => d.value)
    };
  }
  return null;
}

class Autocomplete extends Component {
  constructor(props) {
    super(props);
    this.currentList = [];
    this.selectedOption = -1;
    this.deltaIndex = 0;
    this.state = {
      listLength: 0,
      sortedItems: [],
      flatList: []
    };
  }

  static getDerivedStateFromProps(props, state) {
    // console.log('getDerivedStateFromProps',props,state)

    computeList(props.dataList, state);

    // Return null to indicate no change to state.
    return null;
  }
  onInputChange(el, force = false) {
    if (this.autocomplete.value.length > 0) {
      this.cleanInput.classList.add('focus');
    } else {
      this.cleanInput.classList.remove('focus');
    }
    if (this.autocomplete.value.length < 3) {
      this.currentList = [];
      this.itemsList.innerHTML = '';
    }
    // if((this.searchStr !== this.autocomplete.value && this.autocomplete.value.length >= 3) || force ) {
    if (this.autocomplete.value.length >= 3 || force) {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        let regex = new RegExp('', 'gi');
        try {
          this.searchStr = this.autocomplete.value.replace(
            /[\[\(\]\(\)\{\}\|\~\.\\!@#$%^&*_]/g,
            ''
          );
          let regexp = `^${this.searchStr}|\\s${this.searchStr}`;
          const splitSearchStr = this.searchStr.split(' ');
          if (splitSearchStr.length === 2) {
            const invertedString = `${splitSearchStr[1]}.*${splitSearchStr[0]}`;
            regexp = `${regexp}|(^${invertedString})|(\\s)${invertedString}}`;
          }
          regex = new RegExp(regexp, 'gi');
        } catch (e) {
          console.log('funny characters in your string search');
        }

        // console.log(regexp);

        const max = 10;
        this.currentList = [];

        if (this.searchStr.length >= 3) {
          for (let index = 0; index < this.state.flatList.length; index++) {
            const name = this.state.flatList[index];
            if (name.search(regex) > -1) {
              // console.log('ok')
              this.currentList.push(index);
            }
            if (this.currentList.length >= max + this.deltaIndex) {
              break;
            }
          }
        }
        // console.log(':::', this.currentList.length, this.deltaIndex, max, [
        //   ...this.currentList
        // ]);

        //if(this.currentList.length > max) {
        const oldCurrentList = [...this.currentList];
        this.currentList = this.currentList.slice(this.deltaIndex);
        this.currentList =
          this.currentList.length < max ? oldCurrentList : this.currentList;
        //}
        // this.selectedOption = this.deltaIndex > 0 ? (this.currentList.length - (1 + this.deltaIndex)) : 0;

        this.itemsList.innerHTML = this.currentList
          .map((d, i) => {
            const label = this.state.sortedItems[d].label;
            const splitLabel = label.split('-');
            const name = label.replace(splitLabel[splitLabel.length - 1], '');
            // console.log(splitLabel)
            return `<li class='${
              i === this.selectedOption ? 'selected' : ''
            }'>${name.replace('-', ' ')} <span>${
              splitLabel[splitLabel.length - 1]
            }</span></li>`;
          })
          .join('');
        this.itemsList.querySelectorAll('li').forEach((d, i) => {
          d.addEventListener('click', () => {
            this.selectItem(i);
          });
        });
      }, 150);
    }
  }
  clean() {
    this.itemsList.innerHTML = '';
    this.autocomplete.value = '';
    this.currentList = [];
    this.deltaIndex = 0;
    this.cleanInput.classList.remove('focus');
  }

  onKeyDown(el) {
    const c = el.keyCode;
    const li = this.itemsList.querySelectorAll('li');

    // console.log('---->', this.currentList, this.selectedOption);
    if(!this.currentList.length) {
      return;
    }
    if (c === 9) {
      this.selectedOption = 0;
      this.deltaIndex = 0;
    }
    if (c === 13 || c === 9) {
      if (!~this.selectedOption) {
        this.selectedOption = 0;
        this.deltaIndex = 0;
      }
      if (this.props.callback) {
        // console.log(
        //   'callback',
        //   this.selectedOption,
        //   this.currentList[this.selectedOption],
        //   this.state.sortedItems[this.currentList[this.selectedOption]]
        // );
        this.props.callback(
          this.state.sortedItems[this.currentList[this.selectedOption]],
          true
        );
      }

      this.autocomplete.value = this.state.sortedItems[
        this.currentList[this.selectedOption]
      ].name;
      this.itemsList.innerHTML = '';
      this.selectedOption = -1;
      this.deltaIndex = 0;
      // this.currentList = [];
    }
    if (c === 40) {
      el.preventDefault();

      if (this.selectedOption >= this.currentList.length - 1) {
        this.deltaIndex += 1;
        this.onInputChange(null, true);
      }

      this.selectedOption =
        this.selectedOption < this.currentList.length - 1
          ? this.selectedOption + 1
          : this.currentList.length - 1;

      // console.log(this.selectedOption,this.deltaIndex)
      if (li.length) {
        // console.log('selectedOption', this.selectedOption)
        li.forEach(l => l.classList.remove('selected'));
        li[this.selectedOption].classList.add('selected');
      }
    }
    if (c === 38) {
      el.preventDefault();

      if (this.selectedOption === 0 && this.deltaIndex > 0) {
        this.deltaIndex -= 1;
        this.onInputChange(null, true);
      }

      this.selectedOption =
        this.selectedOption - 1 >= 0 ? this.selectedOption - 1 : 0;

      if (li.length) {
        li.forEach(l => l.classList.remove('selected'));
        li[this.selectedOption].classList.add('selected');
      }
    }
    if (c !== 40 && c !== 38 && c !== 13) {
      // this.selectedOption = -1;
      // this.deltaIndex = 0;
    }
    if (c === 27) {
      this.clean();
    }
  }

  selectItem(index) {
    if (this.props.callback) {
      this.props.callback(this.state.sortedItems[this.currentList[index]]);
    }
    this.autocomplete.value = this.state.sortedItems[
      this.currentList[index]
    ].name;
    this.itemsList.innerHTML = '';
    this.currentList = [];
    this.deltaIndex = 0;
    this.selectedOption = 0;
  }


  setList(list=[]) {
    this.setState(computeList(list,this.state));
  }

  render() {
    const { lang } = this.props;
    const { sortedItems } = this.state;
    // console.log("render", this.state);
    return (
      <div className="autocomplete">
        {/*<span className="story-anchor" data="search" />*/}
        {/*<label htmlFor="playerSearch">{lang==='en'?'Look for your favourite players':'Cerca i tuoi calciatori preferiti'}</label>*/}
        <div className="input-container">
          <input
            type="text"
            ref={el => (this.autocomplete = el)}
            onChange={el => this.onInputChange(el)}
            name="playerSearch"
            onKeyDown={el => this.onKeyDown(el)}
            placeholder={
              sortedItems.length ? (lang==='en'?'Enter a player name':'Cerca un giocatore') : 'loading...'
            }
            autoCapitalize="off"
            autoComplete="off"
            autoCorrect="off"
          />
          <div className="search-input"></div>
          <div ref={el => (this.cleanInput = el)} className="clean-input" onClick={() => this.clean()}><i className="glyphicon glyphicon-search"></i><i className="glyphicon glyphicon-remove-circle"></i></div>
        </div>
        <div className="data-list">
          <ul ref={el => (this.itemsList = el)} />
        </div>
      </div>
    );
  }
}

export default Autocomplete;



