export const translateTeam = team => {
  if (!team) {
    //console.log('team', team);
    return team;
  }
  const translations = {
    pausiert: 'Pausa',
    jugend: 'Giovani',
    vereinslos: 'Senza contratto',
    unbekannt: 'Sconosciuto'
  };

  for (let text in translations) {
    const regex = new RegExp(text, 'gi');
    team = team.replace(regex, translations[text]);
  }
  const encodedName = team;
  try {
    team = decodeURI(team);
  } catch (e) {
    team = encodedName;
  }
  return team;
};



