import React, { Component } from "react";
import { extent } from "d3-array";
import BarChart from '../BarChart/BarChart';

// import competitionsPlayers from '../playersInLeagues.json';

import "./competitions.css";

const FIELDS = ['max','avg'];

class CompetitionsComparison extends Component {
  constructor(props) {
    super(props);

    // this.competitionsList = props.competitionsList.filter(d => {
    //   return d.leagueID.search(/(2|3)/gi) === -1;
    // }).map(d => {
    //   const players = competitionsPlayers.find(l => l.league === d.league);
    //   if(players) {
    //     d.players = players.players;
    //   }
    //   return d;
    // })
    // console.log(this.competitionsList)
    this.state = {}
  }
  // async componentDidMount() {
  //   const res = await fetch(
  //     `assets/transfers/data/yearsSameTeamByLeague.json`
  //   );
  //
  //   const competitionsStats = await res.json();
  //
  //   // console.log(competitionsStats)
  //   this.setState({
  //     competitionsStats: competitionsStats.map(d => {
  //       const league = this.props.competitionsList.find(l => l.league === d.league);
  //       if(league) {
  //         d.country = league.country;
  //         d.leagueName = league.leagueName;
  //         d.players = league.players;
  //       }
  //       return d;
  //     }).filter(d => d.leagueName)
  //     .filter(d => d.players > this.props.playersThreshold).sort((a,b) => b[SORTBY] - a[SORTBY]),
  //   })
  // }
  async componentDidMount() {
    const res = await fetch(
      `assets/transfers/data/leaguesStats.json`
    );

    const competitionsStats = await res.json();

    this.setState({
      competitionsStats,
    });
  }
  render() {
    const {competitionsStats} = this.state;


    if(!competitionsStats) {
      return null;
    }
    // console.log(JSON.stringify(competitionsStats,null,2))
    const extents = {};
    FIELDS.forEach(field => {
      extents[field] = extent(competitionsStats, d => d[field]);
    })

    return <div className="competitions">
      <BarChart data={competitionsStats.map(d => {
        d.key = `${d.leagueName}-${d.country}`;
        d.title = <span>{d.leagueName}<br/>{d.country}</span>;
        return d;
      })} fields={FIELDS} extents={extents} label="anni"/>
    </div>;

  }
}

export default CompetitionsComparison;



